<template>
  <b-card>
    Lista de asistencia
    <b-row>
      <b-col>
        <b-form @submit.prevent="onSubmit">
          <b-form-group
            label-class="text-uppercase"
            label="Ingresar DNI"
          >
            <b-form-input
              v-model="numero_documento"
              placeholder="Ingresar DNI"
            />
          </b-form-group>
          <b-button
            type="submit"
            variant="primary"
          >
            Registrar
          </b-button>
        </b-form>
      </b-col>
      <b-col>
        <b-card
          header-class="text-uppercase font-weight-bold"
          border-variant="success"
          :header="dataResponse.comida"
          align="center"
        >
          {{ dataResponse.usuario.name }} <br>
          {{ dataResponse.fecha_registro }} <br>
          {{ dataResponse.mensaje }}
        </b-card>
      </b-col>
    </b-row>

    <b-table
      id="my-table"
      ref="refTablaUsuarios"
      class="position-relative"
      show-empty
      head-variant="light"
      hover
      small
      responsive
      :busy.sync="isBusy"
      :items="myProvider"
      :fields="fields"
      :current-page="currentPage"
      :empty-text="'No hay registros para mostrar'"
      :empty-filtered-text="'No hay registros que coincidan con su solicitud.'"
      :per-page="perPage"
      :filter="filter"
      :filter-included-fields="filterOn"
      :sort-by="sortBy"
      :sort-desc="sortDesc"
    >
      <template #table-busy>
        <div class="text-center text-danger my-2">
          <b-spinner class="align-middle" />
          <strong>Cargando...</strong>
        </div>
      </template>

    </b-table>
    <div class="mx-2 mb-2">
      <b-row>
        <b-col
          cols="12"
          sm="6"
          class="d-flex align-items-center justify-content-center justify-content-sm-start"
        >
          <span class="text-muted">Mostrando registros del {{ from }} al {{ to }} de un total de {{ totalRows }} registros</span>

        </b-col>
        <!-- Pagination -->
        <b-col
          cols="12"
          sm="6"
          class="d-flex align-items-center justify-content-center justify-content-sm-end"
        >

          <b-pagination
            v-model="currentPage"
            :total-rows="totalRows"
            :per-page="perPage"
            first-number
            last-number
            class="mb-0 mt-1 mt-sm-0"
            prev-class="prev-item"
            next-class="next-item"
          >
            <template #prev-text>
              <feather-icon
                icon="ChevronLeftIcon"
                size="18"
              />
            </template>
            <template #next-text>
              <feather-icon
                icon="ChevronRightIcon"
                size="18"
              />
            </template>
          </b-pagination>

        </b-col>
      </b-row>
    </div>
  </b-card>
</template>

<script>
import {
  BForm, BFormGroup, BFormInput, BButton, BCard, BTable, BSpinner, BPagination, BRow, BCol,
} from 'bootstrap-vue'
import { onUnmounted } from '@vue/composition-api'
import Ripple from 'vue-ripple-directive'
import store from '@/store'
import asistenciaBecariosStoreModule from './asistenciaBecariosStoreModule'

export default {
  components: {
    BForm, BFormGroup, BFormInput, BButton, BCard, BTable, BSpinner, BPagination, BRow, BCol,
  },
  directives: {
    Ripple,
  },
  data() {
    return {
      numero_documento: null,
      comida: null,
      dataResponse: {
        usuario: {
          name: '',
        },
      },
      isBusy: false,
      mostrar: false,
      items: [],
      fields: [
        { key: 'usuario_id', label: 'comensal', sortable: true },
        {
          key: 'comida', label: 'comida', sortable: true, class: 'text-uppercase',
        },
        { key: 'fecha_registro', label: 'fecha_registro', sortable: true },
      ],
      from: 1,
      to: 1,
      totalRows: 1,
      currentPage: 1,
      perPage: 5,
      pageOptions: [5, 10, 15, 100],
      sortBy: 'id',
      sortDesc: false,
      sortDirection: 'desc',
      filter: '',
      filterOn: [],
    }
  },
  methods: {
    onSubmit() {
      const formData = {
        numero_documento: this.numero_documento,
        // comida: this.almuerzo,
        comida: 'almuerzo',
      }

      store.dispatch('asistencia/addBecarioAsistencias', formData)
        .then(response => {
          console.log(response)
          this.dataResponse = response.data
        })

      console.log('Enviando asistencia', formData)
    },
    myProvider(ctx) {
      // eslint-disable-next-line no-param-reassign
      ctx.asistencia = true

      // eslint-disable-next-line no-param-reassign
      ctx.fecha_registro = new Date()

      const promise = store.dispatch('asistencia/fetchBecarioAsistencias', { queryParams: ctx })
      return promise.then(response => {
        const {
          data, total, from, to,
        } = response.data

        const items = data
        this.totalRows = total || 0
        this.from = from || 0
        this.to = to || 0
        return items || []
      })
    },
  },
  setup() {
    const ASISTENCIA_APP_STORE_MODULE_NAME = 'asistencia'

    if (!store.hasModule(ASISTENCIA_APP_STORE_MODULE_NAME)) store.registerModule(ASISTENCIA_APP_STORE_MODULE_NAME, asistenciaBecariosStoreModule)

    onUnmounted(() => {
      if (store.hasModule(ASISTENCIA_APP_STORE_MODULE_NAME)) store.unregisterModule(ASISTENCIA_APP_STORE_MODULE_NAME)
    })

    return {

    }
  },
}
</script>

<style lang="scss" scoped>

</style>
